const currencies = require('./data/currencies')
const parseNumber = require('../numbers/parse')

// const isPenny = `(cent|cents|penny|pennies|ore|sent|ngwee|tambala|penni|grosz|pfennig)`

// aggregate currency symbols for easy lookup
// const subs = {
//   pennies: true,
// }
const symbols = {}

let pennies = {}
currencies.forEach((o) => {
  o.sym.forEach((str) => {
    symbols[str] = symbols[str] || o.iso
  })
  symbols[o.iso] = symbols[o.iso] || o.iso
  if (o.sub) {
    pennies[o.sub] = true
  }
})
// create a match statement with all the penny-units
let isPenny = `(${Object.keys(pennies).join('|')})`

// parse 'australian dollars'
const getNamedCurrency = function (doc) {
  let m = doc.match('#Currency+')
  m.nouns().toSingular() // 'dollars'➔'dollar'
  let str = m.text('reduced')
  return currencies.find((o) => {
    // 'mexcan peso'
    if (str === `${o.dem} ${o.name}`) {
      return o
    }
    // 'CAD'
    if (str === o.iso) {
      return o
    }
    // 'cent'
    if (str === o.sub) {
      return o
    }
    // 'peso'
    if (str === o.name) {
      return o
    }
    // any other alt names
    if (o.alias && o.alias[str] === true) {
      return o
    }
    return false
  })
}

// turn '£' into GBP
const getBySymbol = function (obj) {
  // do suffix first, for '$50CAD'
  if (obj.suffix && symbols.hasOwnProperty(obj.suffix)) {
    return currencies.find((o) => o.iso === symbols[obj.suffix])
  }
  // parse prefix for '£50'
  if (obj.prefix && symbols.hasOwnProperty(obj.prefix)) {
    return currencies.find((o) => o.iso === symbols[obj.prefix])
  }
  return null
}

// five dollars and six cents -> 5.06
const parseMoney = function (doc) {
  // support 'and five cents' as a decimal
  let decimal = 0
  let decimalEnd = doc.match(`and #Money+ ${isPenny}`)
  if (decimalEnd.found) {
    doc = doc.not(decimalEnd)
    let res = parseNumber(decimalEnd.match('#Value+'))
    if (res && res.num) {
      decimal = res.num / 100
    }
  }
  let res = parseNumber(doc)
  let num = res.num || 0
  num += decimal

  let found = getBySymbol(res) || getNamedCurrency(doc) || {}
  let sym = ''
  if (found && found.sym) {
    sym = found.sym[0]
    // make '50 cents' -> 0.50
    if (num && doc.has(`${isPenny}`)) {
      num = num / 100
    }
  }
  return {
    num: num,
    iso: found.iso,
    demonym: found.dem,
    currency: found.name,
    plural: found.plural,
    symbol: sym,
  }
}
module.exports = parseMoney
